.mod-back {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100% !important;
  height: 100% !important;
  z-index: 9;
  background-color: rgba(0, 0, 0, 0.15);
  opacity: 0;
  transition: all 0.5s;
  animation-name: opacity;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
}

@keyframes opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.mod-form {
  width: 80%;
  max-width: 300px;
  height: 330px;
  background-color: rgb(255, 255, 255);
  margin-bottom: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  transition: all 0.5s;
  animation-name: down;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
  user-select: none;
}

@keyframes down {
  0% {
    margin-bottom: 150px;
  }
  100% {
    margin-bottom: 50px;
  }
}

.mod-form > div > h4 {
  font-size: 21px;
  margin: 0;
  text-transform: uppercase;
  padding-right: 65px;
  color: rgb(84, 84, 84);
  cursor: default;
}

.mod-form > div > label {
  color: rgb(100, 100, 100);
  font-weight: 900;
}

.mod-form > div > input {
  width: 250px;
  border: none;
  /* background-color: rgb(239, 239, 239); */
  border-bottom: solid 2px rgb(130, 130, 130);
  border-radius: 0;
  font-size: 20px;
  padding: 5px;
  color: rgb(79, 79, 79);
  transition: all 0.3s;
}

.mod-form > div > input:hover {
  background-color: rgb(245, 245, 245);
}

.mod-form > div > input:focus {
  outline: none;
  border-bottom: solid 2px rgb(130, 130, 130);
}

.mod-form:hover {
  box-shadow: 0px 0px 4px 1px rgba(0, 0, 0, 0.201);
}

.mod-form > div > input[type="button"] {
  background-color: rgb(88, 88, 88);
  color: white;
  text-transform: uppercase;
  border-bottom: none;
  border-radius: 4px;
  box-shadow: 1px 1px 3px 0px black;
  cursor: pointer;
  transition: all 0.3s;
  height: 45px;
}

.mod-form > div > input[type="button"]:hover {
  background-color: rgb(46, 46, 46);
  box-shadow: 0px 0px 5px 0px black;
}

.cancel {
  position: relative;
  width: 30px;
  height: 30px;
  top: 50px;
  left: 37%;
  background-color: rgba(0, 0, 0, 0);
  cursor: pointer;
}

.cancel > span {
  position: absolute;
  width: 30px;
  height: 4px;
  top: 45%;
  transform: rotate(45deg);
  background-color: rgb(133, 133, 133);
}

.cancel:hover > span,
.cancel:hover > span::after {
  background-color: rgb(112, 112, 112);
}

.cancel > span::after {
  position: absolute;
  content: "";
  width: 30px;
  height: 4px;
  background-color: rgb(133, 133, 133);
  transform: rotate(90deg);
}

.unMount {
  opacity: 0;
}
