.searchbar-container {
  flex-grow: 1;
  max-width: 500px;
  display: flex;
  justify-content: center;
  height: 40px;
  margin-left: 10px;
}

.searchbar-container > span {
  position: relative;
  top: 7px;
  left: -32px;
  width: 25px;
  height: 25px;
  background-image: url("../assets/search.png");
  background-size: contain;
  background-repeat: no-repeat;
  cursor: pointer;
  transition: all 0.2s;
}

.searchbar-container > input {
  width: 90%;
  border: none;
  box-shadow: 1px 1px 4px 1px rgba(0, 0, 0, 0.409);
  border-radius: 5px;
  padding: 10px;
  color: rgb(150, 150, 150);
}

.searchbar-container > input:focus {
  outline: none;
}

.searchbar-container > input::placeholder {
  color: rgb(150, 150, 150);
}

.searchbar-container > input:hover {
  box-shadow: 1px 1px 4px 1px rgba(0, 0, 0, 0.593);
}
