.notification-container {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.819);
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s;
  animation: showMenu 0.5s;
}

@keyframes showMenu {
  0% {
    opacity: 0;
    transform: translate(0px, 100%);
  }
  100% {
    opacity: 1;
    transform: translate(0px, 0%);
  }
}

.notification-menu {
  width: 100%;
  height: 100%;
  background-color: rgb(255, 255, 255);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 0px;
}

.notification-title {
  /* padding-right: 80px; */
  width: 100%;
  font-size: 22px;
  margin: 0px;
  background-color: rgb(0, 0, 0);
}
.notification-title > h2 {
  margin: 15px;
  color: rgb(255, 255, 255);
}

.cancelButton {
  position: absolute;
  top: 20px;
  right: 10px;
  width: 40px;
  height: 40px;
  cursor: pointer;
  z-index: 99;
}
.cancelButton:hover > span,
.cancelButton:hover > span::after {
  background-color: rgb(222, 222, 222);
  width: 39px;
}

.cancelButton > span {
  position: absolute;
  top: 18px;
  left: 0px;
  width: 40px;
  height: 4px;
  background-color: rgb(255, 255, 255);
  transform: rotate(45deg);
}
.cancelButton > span::after {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  width: 40px;
  height: 4px;
  background-color: rgb(255, 255, 255);
  transform: rotate(90deg);
}

.notifications {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.131);
  overflow-y: scroll;
}
.notifications::-webkit-scrollbar{
  display: none;
}

body::-webkit-scrollbar{
  display: none;
}