.product-card {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 20px;
  margin-top: 10px;
  width: 100%;
  box-shadow: 0px 0px 2px 0.6px black;
  animation: showCard 0.5s;
}

@keyframes showCard {
  0% {
    opacity: 0;
    transform: translateY(100px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}

.product-name {
  width: 60%;
  overflow-wrap: break-word;
  padding-left: 5px;
}

.quantity {
  position: absolute;
  right: 100px;
}

input[type="number"] {
  width: 45px;
  height: 35px;
  font-size: 17px;
  border-radius: 5px;
  text-align: center;
  font-weight: 600;
  border: solid 3px black;
}

.icons-container {
  position: absolute;
  right: 10px;
  display: flex;
  gap: 5px;
}

.icons-container > span > img {
  height: 35px;
  justify-self: right;
  user-select: none;
  -webkit-user-drag: none;
}

.icons-container > span > img:hover {
  filter: grayscale(50%);
  transform: scale(1.05);
  cursor: pointer;
}
.icons-container > span:first-child > img:hover {
  background-color: rgba(0, 0, 0, 0.165);
}

