.cards-container {
  margin-bottom: 100px;
}

.nav {
  width: 100%;
  height: auto;
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.reload {
  width: 100%;
  height: auto;
  position: relative;
  display: flex;
  justify-content: flex-end;
}
.reload > div {
  transition: all 0.3s;
  position: relative;
  right: 10px;
  width: 140px;
  text-align: center;
  border: solid 3px black;
  padding: 7px;
  border-radius: 5px;
  margin-top: 7px;
  cursor: pointer;
  /* opacity: 0; */
}

.reload > div > p {
  margin: 0px !important;
  padding: 0 !important;
  font-weight: 900;
}

.reload > div:hover {
  background-color: rgb(237, 237, 237);
}

.show {
  animation: show 0.5s;
}

@keyframes show {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.loader {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 48px;
  height: 48px;
  border: 5px solid rgb(0, 0, 0);
  border-bottom-color: transparent;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
