
.filter-container {
  position: fixed;
  display: flex;
  justify-content: center;
  width: 100%;
  height: 45px;
  bottom: -10px;
  /* top: 0; */
}

.filters-icon {
  display: flex;
  justify-content: center;
  position: fixed;
  width: 80px;
  height: 50px;
  background-color: black;
  border-radius: 20px 20px 0px 0px;
  cursor: pointer;
}

.filters-icon:hover {
  transform: translateY(-4px);
}

.filters-icon > span {
  position: absolute;
  width: 50px;
  height: 4px;
  top: 10px;
  left: 15px;
  background-color: rgb(255, 255, 255);
}

.filters-icon > span::after {
  content: "";
  position: absolute;
  width: 50px;
  height: 4px;
  top: 10px;
  background-color: rgb(255, 255, 255);
}
