.agregar {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 150px;
  padding-right: 10px;
}

.agregar > .agregar-button {
  width: 140px;
  height: 50px;
  border: solid 4px rgb(208, 208, 208);
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: all 0.2s;
  border-radius: 10px;
}

.agregar > .agregar-button:hover,
.agregar > .agregar-button:hover > span {
  border: solid 4px rgb(182, 182, 182);
}
.agregar > div:hover > span {
  border: solid 4px rgb(182, 182, 182);
}

.agregar > .agregar-button:hover {
  transform: scale(1.04);
}

.agregar > .agregar-button:hover > span::before,
.agregar > .agregar-button:hover > span::after {
  background-color: rgb(182, 182, 182);
}

.agregar > .agregar-button > span {
  position: relative;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: solid 4px rgb(208, 208, 208);
}

.agregar > .agregar-button > span::before,
.agregar > .agregar-button > span::after {
  transition: all 0.2s;
  content: "";
  position: absolute;
  top: 45%;
  left: 1px;
  width: 30px;
  height: 4px;
  background-color: rgb(208, 208, 208);
  border-radius: 5px;
}

.agregar > .agregar-button > span::before {
  transform: rotate(90deg);
  top: 45%;
}
