.notification-card {
  display: grid;
  grid-template-columns: 1fr 6fr 1fr;
  justify-items: center;
  align-items: center;
  width: 100%;
  height: auto;
  min-height: 70px;
  background: rgb(255, 255, 255);
  margin-bottom: 2px;
}

.icon {
  background-image: url("../assets/notificacion.png");
  background-repeat: no-repeat;
  background-size: contain;
  width: 40px;
  height: 40px;
  margin-left: 5px;
}

.acept {
  background-image: url("../assets/confirmar.png");
  background-repeat: no-repeat;
  background-size: contain;
  width: 45px;
  height: 45px;
  margin-right: 5px;
}

.details-container {
  width: 100%;
  height: 100%;
  max-width: 100%;
  padding-left: 5px;
  padding-right: 10px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  overflow: hidden;
}

.details-container > p {
  font-size: 16px;
  font-weight: 900;
}

.product-name-notification {
  color: red;
}
